import React, { Component } from 'react'
import styled from 'styled-components'

const InputField = styled.input`
    text-align:center;
    margin-bottom:2em;
    width:30em;
`

const Flex = styled.div`
    display:flex;
    flex-direction: ${props => props.direction || "column"};
    align-items: ${props => props.alignItems || "center"};
    justify-content: ${props => props.justify || "center"};
`

const ColorBox = styled.span`
    display:flex;
    width:13px;
    height:13px;
    background-color:${props => props.color || "red"};
    border: 1px solid black;
    margin-left:5px;
    border-radius:3px;
    &::focus {
        border:5px solid black;
    }`

export default class SouvenirForm extends Component {

    state={
        name:"",
        location:"",
        color:"",
    }

    pickColor = (color) => {
        let styling = 
        this.setState({
            color:color
        })
    } 
  render() {
    return (
        <Flex>

            <InputField type="text" placeholder="Name"/>
            <InputField type="text" placeholder="Location"/>
            <InputField type="text" placeholder="Add song link"/>
            <Flex direction="row">

                <ColorBox color="#DC143C" onClick={() => (this.pickColor("#DC143C"))}/>
                <ColorBox color="#87CEFA" onClick={() => (this.pickColor("#87CEFA"))}/>
                <ColorBox color="#1E90FF" onClick={() => (this.pickColor("#1E90FF"))}/>
                <ColorBox color="#00FF7F" onClick={() => (this.pickColor("#00FF7F"))}/>
                <ColorBox color="#FA8072" onClick={() => (this.pickColor("#FA8072"))}/>



                <ColorBox color={this.state.color}/>
                <button>Start writing</button>
            </Flex>
        </Flex>
    )
  }
}
